<!---------------  商品描述  -------------------->
<template>
  <div class="container-fluid" >
    <!---------------  上方目錄  -------------------->
    <b-breadcrumb style="margin-top:20px;">
      <b-breadcrumb-item>
        <router-link to="/" >
          <b-icon icon="house-fill" scale="1.25" shift-v="1.25" aria-hidden="true"></b-icon>
            所有商品
        </router-link>
      </b-breadcrumb-item>
      <b-breadcrumb-item active>商品詳細資訊</b-breadcrumb-item>
    </b-breadcrumb>
      <div class="item">
        <b-row>
          <!---------------  商品描述區域  左半部-------------------->
          <div class="col-md-6">
            <b-carousel
              id="carousel"
              controls
              indicators
              no-hover-pause
            >
              <b-carousel-slide
                v-for="(image,index) in images"
                :key="index"
              >
                <template v-slot:img>
                  <img
                  class="carousel-image"
                  :src="image.ImageSrc"
                  alt="image slot">
                </template>
              </b-carousel-slide>
            </b-carousel>
          </div>
          <!---------------  商品描述區域 右半部  -------------------->
          <div class="col-md-6">
            <div class="info">
              <b-row class="info-row" cols="2">
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">商品編號</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">{{items.ProductNo}}</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">商品名稱</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">{{items.ProductTitle}}</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">商品售價</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6"><a style="color:red;">${{items.ProductPrice}}</a></b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">商品規格</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">
                  <b-form-select 
                    v-model="specificationselected" 
                    size="sm" 
                    @change="specificationChange"
                    style="width:10vh;"
                    class="mt-3">
                    <b-form-select-option  :value="i" v-for="(s,i) in specification" :key=i>
                      {{s.itemSpecification}}
                    </b-form-select-option>
                  </b-form-select>
                </b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">商品尺寸</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">
                  <b-form-select 
                    v-model="measurementselected" 
                    size="sm" 
                    style="width:10vh;"
                    class="mt-3">
                    <b-form-select-option :value="m.itemMeasurement" v-for="(m,j) in measurement" :key=j>
                      {{m.itemMeasurement}}
                    </b-form-select-option>
                  </b-form-select>
                </b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">商品數量</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">
                  <b-form-spinbutton 
                    id="sb-inline" 
                    v-model="quantity" 
                    min="1" max="100"
                    size="sm">
                  </b-form-spinbutton>
                  </b-col>
              </b-row>
              <hr>
              <center>
                <b-button  style="width:100%;" variant="danger" v-on:click="cartClicked">加入購物車</b-button>
              </center>                
            </div>
          </div>  
        </b-row>
      </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      // ---routere接收的參數(ProductNo)------
      productNo:this.$route.params.id,  
      items:'', 
      // -------------ProductNo 包含的 Item 
      images:[], // ProductNo 包含的 Image 
      specificationselected: null,  // 規格 selected
      specification: [], // Item 規格
      measurementselected: null, // 尺寸 selected
      measurement: [], // Item 尺寸
      quantity:1, // 數量
    }
  },mounted(){
    //  撈 ProductNo 的資料
    this.$http.get(`http://api.marcuschen.cc:443/Product/GetProduct?no=${this.productNo}`)
      .then(response => {
        this.items = response.data[0]
      });
    //  撈  ProductNo裡所有Item 的 Image 圖片
    this.$http.get(`http://api.marcuschen.cc:443/Product/GetProductImage?no=${this.productNo}`)
      .then(response => {
        this.images = response.data;
      });  
    //  撈 ProductNo裡所有Item的規格資料
    this.$http.get(`http://api.marcuschen.cc:443/Product/GetItem?no=${this.productNo}`)
      .then(response => {
        this.specification = response.data;
      });
  },computed:{
    
  },methods:{
     //  選擇規格後 顯示 Item 裡相對應的尺寸資料
    specificationChange(i){
        this.measurement = this.specification[i].measurements
    },
    cartClicked(){
      if(this.$store.state.items.some(item => item.ProductNo === this.items.ProductNo)){
            this.$root.$bvToast.toast(
              `「${this.items.ProductTitle}」已從購物車取消`,
              {
                title: "小提醒",
                autoHideDelay: 2000,
                appendToast: true
              }
            );
            var i = this.$store.state.items.findIndex(x => x.ProductNo === this.items.ProductNo);
            this.$store.commit("cartDelete",i)
      }else{
        this.$root.$bvToast.toast(`「${this.items.ProductTitle}」已加入購物車`, {
            title: "小提醒",
            autoHideDelay: 2000,
            appendToast: true
          });
        this.$store.commit({
            type:"cartInsert", 
            ProductNo: this.items.ProductNo,
            ProductTitle:this.items.ProductTitle,
            ProductPrice:this.items.ProductPrice,
            ProductQantity:this.quantity,
            ProductSpecification:(this.specificationselected!=null)?this.specification[this.specificationselected].itemSpecification:null,
            ProductMeasurement:(this.measurementselected)?this.measurementselected:null
        })
      }
    },
  }
}
</script>
<style>
.item{
  padding: 20px;
  max-height: 100%;
  max-width: 100%;
  margin:auto;
  height:inherit; /*自動填滿瀏覽器高度*/
  display: flex;
}
#carousel{
  width: 100%; 
  height: 100%; 
  max-width:  100%;
  display:  flex;
  align-items: center;
  justify-content:  center;
  overflow: hidden;
  padding:30px;
  
}
.carousel-image{
  max-width: 100%; 
  max-height: 100%; 
}
.info{
  margin-top: 50px;
  padding:10px;
}

.info .info-col{
  padding:10px;
  font-size: 1em;
  font-weight: bold;
  display:  flex;
  align-items: center;
  justify-content:  center;
}

</style>


